<template>
  <div style="width: 100%;height: 100%;">
    <div style="margin: 10px 0;">
      公司：
      <el-select size="mini" v-model="value" @change="display">
        <el-option v-for="item in displayOptions" :key="item.value" :label="item.label" :value="item.value">
        </el-option>
      </el-select>
    </div>
    <div style="margin-bottom: 10px;">
      <el-button type="primary" size="mini" @click="save">保存数据</el-button>
    </div>
    <div>
      <el-table :data="tableData" style="width: 100%;margin-bottom: 20px;" row-key="id" border default-expand-all
        :tree-props="{children: 'children', hasChildren: 'hasChildren'}">
        <el-table-column prop="grade" label="等级" width="180" align="center"></el-table-column>
        <el-table-column label="佣金" align="center">
          <template slot-scope="scope">
            <el-input-number v-model="scope.row.tax" :precision="2" :min="0"></el-input-number>
          </template>
        </el-table-column>
        <el-table-column label="达标金额" align="center">
          <template slot-scope="scope">
            <el-input-number v-model="scope.row.money":precision="0" :min="0"></el-input-number>
          </template>
        </el-table-column>
        <el-table-column label="直推人数" align="center">
          <template slot-scope="scope">
            <el-input-number v-model="scope.row.personcount":precision="0" :min="0"></el-input-number>
          </template>
        </el-table-column>
        <el-table-column label="下级人数" align="center">
          <template slot-scope="scope">
            <el-input-number v-model="scope.row.soncount":precision="0" :min="0"></el-input-number>
          </template>
        </el-table-column>
      </el-table>
    </div>

  </div>
</template>

<script>
  import axios from 'axios'

  export default {
    name: 'index',
    data() {
      return {
        tableData: [],
        displayOptions: [],
        value: '1',
      }
    },
    // 数据请求创建
    created() {
      this.selectRequest();
      this.DataRequest();
    },

    methods: {
      selectRequest() {
        axios({
          method: 'post',
          url: 'https://manage.kaxinkeji.com/api/wanlshop/kaxin/getbrand',
          data: {},
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          },
        }).then((res) => {
          this.value = res.data.data.data[0].id
          for (let i = 0; i < res.data.data.data.length; i++) {
            this.displayOptions.push({
              label: res.data.data.data[i].name,
              value: res.data.data.data[i].id,
            })

          }
        });
      },

      DataRequest() {
        axios({
          method: 'post',
          url: 'https://manage.kaxinkeji.com/api/wanlshop/kaxin/gettaxdetal',
          data: {
            id: this.value
          },
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          },
        }).then((res) => {
          for (let i = 0; i < res.data.data.data.length; i++) {
            this.taxNum = res.data.data.data[i].tax
            this.tableData.push({
              tax: res.data.data.data[i].tax,
              money: res.data.data.data[i].money,
              personcount: res.data.data.data[i].personcount,
              soncount: res.data.data.data[i].soncount,
              grade: `L` + res.data.data.data[i].id,
            })
          }

        });
      },

      display() {
        axios({
          method: 'post',
          url: 'https://manage.kaxinkeji.com/api/wanlshop/kaxin/gettaxdetal',
          data: {
            id: this.value
          },
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          },
        }).then((res) => {
          this.tableData = [];
          for (let i = 0; i < res.data.data.data.length; i++) {
            this.tableData.push({
              tax: res.data.data.data[i].tax,
              money: res.data.data.data[i].money,
              personcount: res.data.data.data[i].personcount,
              soncount: res.data.data.data[i].soncount,
              grade: `L` + res.data.data.data[i].id,
            })
          }

        });
      },

      save() {
        this.$alert(`是否确定保存修改`, '保存数据', {
          confirmButtonText: '确定',
          callback: action => {
            let str = "";
            let strData = [];
            let strsData = [];
            this.tableData.forEach((item, index) => {
              str = index === 0 ? str : `${str}|`;
              let pd = true;
              for (const key in item) {
                str = pd ? `${str}${item[key]}` : `${str},${item[key]}`;
                pd = false;
              }
              strData.push(item.tax)
              strsData.push(item.money)
            });
            let taxData = strData.join(",");
            let moneyData = strsData.join(",");

            axios({
              method: 'post',
              url: 'https://manage.kaxinkeji.com/api/wanlshop/kaxin/saveInfo',
              data: {
                id: this.value,
                data: str,
                taxData: taxData,
                money: moneyData,
              },
              headers: {
                'Content-Type': 'application/x-www-form-urlencoded'
              },
            }).then((res) => {
              console.log(res)
              if(res.data.code === 1){
                this.tableData = [];
                this.DataRequest();
                this.$message({
                  type: 'success',
                  message: `保存成功`
                });
              }else{
                this.$message.error('保存失败');
              }
            });

          }
        });
      },
    },

  }
</script>

<style>
</style>



